
const add0 = (number) => {
    return number >= 10 ? number : `0${number}`
}
const date = {
    /** 
    * @name 
    * @return {string} YYYYMMDD
    */
    getToday: () => {
        const now = new Date()
        return `${now.getFullYear()}${add0(now.getMonth() + 1)}${add0(now.getDate())}`
    }

}
export default date

